import { Pipe, PipeTransform,Injectable } from '@angular/core';

@Pipe({
  name: 'truncate'
})
@Injectable({
  providedIn:'root'
})
export class TruncatePipe implements PipeTransform {

  transform(text: string, n?: number): string {
    //comprobar el numero de letras de plabra
    //comprobar si la la palabra empiza con la misma letra
   const lastWord : string = text.substring(0,n).split('').pop();
   const textSecondatyWithMargin = text.substring(0,n+10).split(' ')

   console.log(textSecondatyWithMargin)

    // !n si n es igual a 
    return !n || text.length < n ? text : text.substring(0,n) + '...'
  }

}
